const ace = require('ace');
const Range = require('ace/range').Range;
const Autocomplete = require('ace/autocomplete').Autocomplete;
const aceLangTools = require('ace/ext/language_tools');
const Antl4rMode = require('ace/mode/antlr4').Mode;
const aceTheme = require('ace/theme/monokai');
const grammar = require('grammar/devfactory/e2e');

// workaround for ace trying to load things dynamically when we're loading from webpack
ace.config.loadModule = function(moduleName, onLoad) {
    console.debug("Refusing to load ", moduleName);
};
ace.config.set('packaged', true);
ace.config.set('basePath', '/');
ace.config.set('workerPath', '');

const aceMode = new Antl4rMode({ grammar: grammar });

const editor = ace.edit("editor", {
    showPrintMargin: false,
    enableBasicAutocompletion: true,
    enableLiveAutocompletion: false, // use a custom one below
    fontSize: '10pt',
});

editor.session.setMode(aceMode);
editor.renderer.setTheme(aceTheme);

// textCompleter, keyWordCompleter, snippetCompleter;
aceLangTools.setCompleters([ aceLangTools.keyWordCompleter ]);

// autocomplete even if there's no prefix, because we're checking the syntax tree
const alwaysLiveAutocomplete = function(e) {
    const editor = e.editor;
    const hasCompleter = editor.completer && editor.completer.activated;
    // console.log("AC: ", e.command);

    const acCommands = [ 'insertstring', 'Return', 'del', 'backspace' ];
    if (!hasCompleter && acCommands.includes(e.command.name)) {
        var completer = Autocomplete.for(editor);
        completer.autoInsert = false;
        completer.showPopup(editor);
    }
};
editor.commands.on('afterExec', alwaysLiveAutocomplete);

// shortcut to ensure other modules don't need the range type
editor.replaceVariable = function(value) {
    const cp = editor.getCursorPosition();
    const token = editor.getSession().getTokenAt(cp.row, cp.column);
    if (token.type == "string" && token.value.startsWith('"') && token.value.endsWith('"')) {
        const tokenRange = new Range(cp.row, token.start, cp.row, token.start + token.value.length);
        editor.getSession().replace(tokenRange, '"' + value + '"');
        return true;
    } else {
        return false;
    }
};

window.editor = editor;
