define(function (require) {

const Lexer = require('./EndToEndTestLexer').EndToEndTestLexer;
const Parser = require('./EndToEndTestParser').EndToEndTestParser;
const sentenceSnippets = require('./EndToEndTest.snippets.json');

// tokenizer expects metadata to be static, but it isn't, may have changed between antlr4 versions
Object.getOwnPropertyNames(Lexer.prototype).filter(n => n.includes('Name')).forEach(name => {
    Lexer[name] = Lexer.prototype[name];
});

const parser = new Parser();
const entryPoint = parser.ruleNames[0];

const baseSnippets = [
    { value:"Feature: ", caption:"Feature: TICKET", snippet:"Feature: ${1:TICKET}\n\nScenario: ${2:SUMMARY}\nWhen", meta:"feature" },
    { value:"Scenario: ", caption:"Scenario: SUMMARY", snippet:"Scenario: ${1:SUMMARY}\nWhen", meta:"scenario" },
    { value:"\n", caption:"" }
];
const snippets = [ baseSnippets, sentenceSnippets ].flat();

// for types see https://macromates.com/manual/en/language_grammars
const antlrTokenNameToAceTokenType = {};
parser.symbolicNames.forEach((symbolicName, i) => {
    const literalName = parser.literalNames[i];
    let type = null;
    if (symbolicName) {
        if (symbolicName.endsWith('_KW')) {
            type = 'keyword';
        } else if (symbolicName == 'COMMENT') {
            type = 'comment';
        } else if (symbolicName == 'STRING') {
            type = 'string';
        }
    }
    if (type == null && literalName) {
        if (/^[!"\#$%&'()*+,\-./:;<=>?@\[\\\]^_{|}~]+$/.test(literalName)) {
            type = 'punctuation';
        }
    }
    // identifier
    // storage
    // comment
    // string
    // constant
    // variable
    // paren
    antlrTokenNameToAceTokenType[symbolicName] = type || 'text';
});

const snippetPredictor = function(priorText) {
    // a fast shortcut
    // todo: swap autlr4-autosuggest to antlr4-c3 and remove this
    const lastToken = priorText.trim().replace(/.*\s/g, '').toLowerCase();
    if (lastToken == 'when' || lastToken == 'then' || lastToken == 'given') {
        return snippets.filter(s => s.meta == lastToken);
    }
    return null;
};

return {
    Lexer: Lexer,
    Parser: Parser,
    entryPoint: entryPoint,
    antlrTokenNameToAceTokenType: antlrTokenNameToAceTokenType,
    snippets: snippets,
    snippetPredictor: snippetPredictor
};

});
